.btn-islamic {
  background: linear-gradient(135deg, var(--islamic-green) 0%, var(--islamic-green-light) 100%);
  color: white;
  border: 2px solid var(--islamic-gold);
  border-radius: 8px;
  padding: 12px 24px;
  font-weight: 600;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}
.btn-islamic:hover {
  background: linear-gradient(135deg, var(--islamic-green-dark) 0%, var(--islamic-green) 100%);
  border-color: var(--islamic-gold-dark);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(45, 90, 76, 0.3);
}
.btn-islamic::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(212, 168, 83, 0.2), transparent);
  transition: left 0.6s ease;
}
.btn-islamic:hover::before {
  left: 100%;
}
.text-islamic-heading {
  font-family: var(--font-headings);
  color: var(--islamic-green);
  font-weight: 700;
  line-height: var(--line-tight);
}
.text-arabic {
  font-family: var(--font-arabic);
  direction: rtl;
  text-align: right;
  line-height: var(--line-relaxed);
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: var(--islamic-beige);
}
::-webkit-scrollbar-thumb {
  background: var(--islamic-green);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--islamic-green-dark);
}
.nav-link {
  position: relative;
  color: var(--islamic-green);
  font-weight: 500;
  transition: color 0.2s;
  padding-bottom: 2px;
}
.nav-link:hover,
.nav-link:focus {
  color: var(--islamic-gold);
  outline: none;
}
.nav-link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 100%;
  height: 3px;
  background: linear-gradient(90deg, var(--islamic-gold) 0%, var(--islamic-gold-dark) 100%);
  border-radius: 2px;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.3s cubic-bezier(0.4,0,0.2,1);
  z-index: 1;
}
.nav-link:hover::after,
.nav-link:focus::after {
  transform: scaleX(1);
}
.focus-gold:focus-visible {
  outline: 2px solid var(--islamic-gold);
  outline-offset: 2px;
  box-shadow: 0 0 0 3px var(--islamic-gold-dark);
}
.focus-green:focus-visible {
  outline: 2px solid var(--islamic-green);
  outline-offset: 2px;
  box-shadow: 0 0 0 3px var(--islamic-green-light);
}
button:focus-visible, a:focus-visible, input:focus-visible, textarea:focus-visible, select:focus-visible {
  outline: 2px solid var(--islamic-gold);
  outline-offset: 2px;
  box-shadow: 0 0 0 3px var(--islamic-gold-dark);
}
@keyframes ticker-scroll {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}
.ticker-marquee {
  display: inline-block;
  white-space: nowrap;
  animation: ticker-scroll 24s linear infinite;
